		message:
			'D cho c khng trn vn, cm xc ca chng ta vn lun ong y.\n\nBi l\nchng ta  lun c nhau,\nchng ta  cng ha lm mt,\nv chng ta  khng qun Ctrl + S\n kp thi lu k nim thnh file.\n\n---\nChnh nh bn,  a 17: hm nay va c cp nht thm nhng tp tin mi\n\nl s gn kt gia my trm con ngi, th thng c ca ngi trong nhng trang giy, gi  c mt thy tai nghe\n\nl nhng xc cm khng mi, nhng cng cha bao gi l c  ni ra,  ri c ng cm v chia s bi c mt th h\n\nl s nhit huyt ti tn cng,  i cng nhau nh cha bao gi c cng bc, th m dng nh bt k ai trong chng ta cng cha tng thy sut mt thi tui tr \n\nl s tic nui v ht hng khi th by lu mong ch khng cch no trn vn, nhng chnh nhng ting an i v cm thng    nhng ch trng c lp y. \n\n---\nDu cho c mun mng, chng mnh tin rng gi y vn kp  to tp tin mi v bm Ctrl + S chng vo  a ca chng ta\n\nni gi gm li xin li t tn su trong y lng ca chng mnh n vi cc bn, v nhng thiu st v mt mt  khin nhng g l p nht khng hon ho\n\ncng l ni   vit mt li cm n\nv  lun  cnh khi chng mnh cn n,  dnh cho chng mnh tt c nhng li ng vin v thu hiu chn thnh nht  ci lng chng mnh c si m tr li,\n cho chng mnh bit nhng g c ha nhp bi con tim mi l th lm nn s y ,  chng mnh tin rng nhng g va tri qua s khng c th m v ngha tri i. \n\n---\nCon tim bn v mnh gi  chy ht cng sut, hy ng mt gic tht su  no b c ngh ngi v nhng g p  nht s kp thi c lu li\n\nrng vo hm y, khng c mt iu g tic nui, ch c hnh nh 270 con ngi, mc cho trn ngi lm lem nhng mng mu  t, vn nm tay nhau, i bn nhau vi n ci ti trn mi, ming khng ngng h vang:\n\n"mt by hai khng!\nmt by hai khng!\nmt by hai khng!\n..."',
