			"Mnh b  v c cm gic c g va vt qua. N kh t lm, thot ci sp thnh cu hc sinh ri\n\nVui ch, hi bun v bi ri na, khng bit sao my nm trc mnh thi xong mun cy phim lm nhng ln ny thi xong ch mun i hc li tht nhanh\n\nLm Ho m mt m lun nhng cng may l v bit y l ln cui m mnh khng gc lc n\n\nBc vo phng thi vi hai cy bt v bc ra ngoi vi hai chic my bay giy\n\nMnh mun v nh ngay, nhng cng mun  li trng chi vi mi ngi m ti n hc thm nhiu qu\n\nBun ng lm lun, sng nay mnh ng lc 3h \n\nNi tht, c nhng mn mnh khng thch, khng gii v khng cn nhng mnh vn n c trong vi ngy. Mnh ngh y s l ng lc  mnh c gng  ng u vi nhng iu kh chu khc trong tng lai\n\nMnh mng nh mun khc lun v t nay s khng phi i din vi Ho na!\n\nCng hi bun v c sai hi nhiu nhng c th gii ca mnh bng sng khi nghe bn mnh c 8.0 IELTS sau bao thng vt v. Chc mng b nha!\n\nD vui v khng phi thi cc mn kh nhn nh vy na nhng cng s rt nh hi bn, nh nhng khonh khc n bi cng nhau, nh nhng lc ti n lm mnh hoang mang khng bit n g, th m may sao cui cng mi th cng u xui ui lt\n\nMnh nhn tng khun mt v tng tng khng bit chng no gp li h\n\nMng, mng lm. Nhng c mt cht tic nui v s khng cn l do gp trc tip crush na\n\nn bi mt qu ri nn mnh thy khng s bi, s im qu na. Bnh tnh m chun b chin u tip thi\n\nMnh khng trng i vo ton l ho lm nhng vi mn vn th hi mong i mt cht nhng lm bi khng nh mong mun th mnh hn lon mt xu trong bui sng ri thi\n\nLc c bo ch cn 10 pht na l ht gi lm bi, lng mnh nh c mt cm gic g  bng qu, nghn nghn. Ghi xong m , s t v k tn, mnh cn mun v thm ci hnh tim nho nh ch ghi ch na. Sao m nh nhung g c ci th m mnh  lun mun i qua tht nhanh, tht l? Vy m c c nu ko, mt cch tht nc ci \n\nC ngh rng y l ln cui cng mnh ngi trn gh Trn Chuyn lm nhng bi kim tra th mnh li thy trng vng Mc d lc no cng c cm gic nh ngi trn ng la, hao tn bao tm t, cng thm lo lng, nhng sau ny nhn li, nhng k c ny s khng th thay th c, v c mun cch my, c l cng chng th tri nghim thm ln no na\n__\n\nK thi hc k cui cng ca nhng nm cp Ba  chnh thc kt thc i vi nhng ngi con kho 17-20. \n\nTuy mi chng ta u c nhng ni suy t khc nhau sau thi, nhng ng li sau ht vn l s lu luyn ca ci suy ngh , y l ln cui ri, sau ny s khng cn na v ri cn g ngoi nhng khong lng ca nhng g ta cha kp ni m thi gian  khng cho php. \n\nLiu bn cn gi trong mnh tm tnh no cha kp th l khng?",
