		message:
			'MY LP MY LOVE \n\nBa nm tri qua  Trn Chuyn t hn  li trong mi ngi chng ta nhng k nim, nhng khonh khc d vui d bun cng vi tp th m chng ta gi l "lp". C ng rng nhng k nim y ri s phai nha theo nm thng, nhng bn i,\n\nchuyn lp mnh  lu k thnh file,\nlo chi love mt ngy s phai? \n\nNgay by gi, hy cng chng mnh mt ln na khi ng h iu hnh Windows Trn Chuyn v i theo ng dn\n17:/ my lp my love/ [lp ca bn] \n xem iu g  v ang c lu gi li nh!\n\n#mylopmylove\n#clickcolove',
