			"/mu  lc/\n\nngy 31 thng 7 nm 2017\n\nHm nay, mnh  gp mt vi ngi bn mi. V mnh li tip tc gieo mm cu chuyn vn lun k thng nin, rng trong nhng gic m p  nht, mnh lun thy mt mu xanh ngt ca nhng tu l non.\n\nCc bn y nghe ri ngm tht lu, nt mt c hoi kh hiu. Nhon mt n ci trn khun mt vn nu sm, mnh bo:\nng vi, mnh  k cu chuyn ny cho nhiu ngi, qua nhiu nm, v khng phi ai cng hiu ngay. Nhng hy yn tm, s c mt ngy bn nhn ra iu g l quan trng thi.\n\n---\nngy 14 thng 8 nm 2017\n\n gn mt thng k t khi nhng ngi bn mi n. C v h  dn quen mt nhau, v dn quen vi ngi nh ny.\n\nMnh nhn quanh mt hi, nhng khun mt mi  khng cn xa l. Tnh bn p  ny n gia nhng ngi trong h. Nhng, khng phi mi tnh bn u xanh mt nh nhau, nht l khi cha th cng thu hiu v s chia.\n\nNng gt my th ti tm chiu cng du bt thi m\nMnh kh ni, ri dang rng cnh tay  m cc bn di ci nng ca mt tri.\nt nhin, c mt chng su trn tay mnh ri xung khin cho ni vu v hin ti tri i u mt, ng li ch cn ting ht tong xen ln n ci gin ca cc bn.\nMnh ph ci, bi l mt gic m xanh gi ci nh mnh cng ch cn nhn thy mu xanh trong tro th ny thi l  vui c ngy.\n\n---\nngy 14 thng 4 nm 2019\n\nV l Ch nht nn mnh khng c gp nhng ngi bn n. Th nhng, mnh li c c hi tr chuyn vi bng xanh, ngi bn ch ct by lu ng r cng mnh  ngi nh ny.\n\n- Bng xanh bit khng, bnh thng d bn lun c ch  nht nhng nhng ngi bn kia i lc vn s th thn nhn ra ca s v nhn thy tui. Nhng my hm nay s ln cc bn nhn ra ch m trn u ngn tay.\n\n- , y l v nhng ngi bn b nh y sp phi thi hc k ri.\n\n- Nhng cng mt mu xanh lc, my ngy nay t khi no c ch  ti th tui ngh cng bun ch...\n\n- Thi no, bnh thng h chng ngi ni chuyn v chi a vi nhau bn bn sut sao? Tui c chng kin nhng s c gng ht mnh ca h, th bn cng c xem bao nhiu ngy tri, bao nhiu hot ng y sc tr ca h di sn trng m tui u th no thy c. Bn ghen t vi mu xanh lc ca tui, nhng ng qun rng ai cng c mt mu xanh ca ring mnh, v nhng ngi bn kia cng vy. Cn nh mu xanh trong tro ngy no m bn tng trm tr ch? Gi n  l mu xanh rc r ca nhit huyt ri .\n\n-  phi. Mu xanh rc r ca nhit huyt. Sao tui c th qun c? Chnh n  cho tui tr li, chnh n  nhum xanh cho tu l ca tui m.\n\n---\nngy 12 thng 6 nm 2020\n\nHm nay nhng ngi bn  li n bn gp mnh. Cc bn i cng nhau, trng thn thit lm.  th phi thi, v d khng phi cu chuyn c tch th nhng s kt thc di mi nh ny vn thng l kt thc c hu m.\n\nTrong lng hiu r chuyn sp din ra, mnh lin hi:\n- Cc bn  m thy mu xanh ngt ca nhng tu l non cha?\n- Khng cn phi m, ti mnh  nhn thy n ri.  l bn, l bng xanh, cng l mu xanh ca chnh bn thn ti mnh - trong tro vo nhng ngy u tin  ri trn y sc sng trong nhng ngy sau . Tt c, tt c u xanh ngt mu l non.\n- Nu nh vy th tt qu. Bi mu xanh ca cc bn, ch c cc bn mi bit n trng nh th no v  cng nhau nhn thy n, cng nhau gi gn n. Mnh vn ch l mt ci cy nh bao ci cy khc, nhng chnh nh th hng sc sng t nhng g cc bn to ra m nhng tn l ca mnh mi xanh ti theo nm thng. \n- Vy, chng mnh phi lm sao  lu li sc xanh non y? \n- Tht ra, cc bn khng cn lm g c u. D cho sau ny thi gian c a mu, d cho nhng tu l ca mnh khng cn xanh tt th nhng sc xanh non ca cc bn s mi mi c lu li. V tt c nhng th y, vn  m mm mt mng xanh vnh cu trong k c ca cc bn ri.",
